@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;800&family=Roboto:wght@500&family=Titan+One&display=swap');

@layer base {
    body    {
        @apply font-[Poppins] 
    }
    li      {
        @apply px-4;
        @apply cursor-pointer
    }

    .card-content {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        height: 400px;
    }

    .card-content:hover {
        background-image: linearGradient(
        to right,
        rgba(46, 46, 51, 0.8)
        /* hsla(242, 74%, 61%, 0.8) */
        ) !important; 
    }
}